// Search Swiftype.

@import '~styles/freshpress-includes';

// Styles has !important to override default Swiftype CSS.

.search-swiftype {
	&__input {
		width: calc( 100% - 4.5rem ) !important;
		height: auto !important;
		padding: px-to-rem( 13 ) px-to-rem( 16 ) px-to-rem( 14 ) px-to-rem( 56 ) !important;
		font-size: 0.7rem !important;
		line-height: 1.1 !important;
		background-color: #fbfdfe !important;
		background-image: url( data:image/svg+xml;base64, ) !important;
		background-repeat: no-repeat !important;
		background-position: left 21px center !important;
		background-size: px-to-rem( 23 ) px-to-rem( 23 ) !important;
		border: 2px solid #cdd4d9 !important;
		border-radius: 10px !important;

		&:focus {
			box-shadow: none;
		}

		@include media-breakpoint-up( sm ) {
			font-size: 1.2rem !important;
		}

		@include media-breakpoint-up( md ) {
			font-size: 2.2rem !important;
		}
	}

	.st-ui-no-results {
		.st-query-present {
			.st-spelling-suggestion-link {
				display: none;
			}
		}
	}
}
