.search-swiftype__input {
  width: calc( 100% - 4.5rem ) !important;
  height: auto !important;
  padding: 0.8125rem 1rem 0.875rem 3.5rem !important;
  font-size: 0.7rem !important;
  line-height: 1.1 !important;
  background-color: #fbfdfe !important;
  background-image: url(data:image/svg+xml;base64,) !important;
  background-repeat: no-repeat !important;
  background-position: left 21px center !important;
  background-size: 1.4375rem 1.4375rem !important;
  border: 2px solid #cdd4d9 !important;
  border-radius: 10px !important;
}
.search-swiftype__input:focus {
  box-shadow: none;
}
@media (min-width: 480px) {
  .search-swiftype__input {
    font-size: 1.2rem !important;
  }
}
@media (min-width: 768px) {
  .search-swiftype__input {
    font-size: 2.2rem !important;
  }
}
.search-swiftype .st-ui-no-results .st-query-present .st-spelling-suggestion-link {
  display: none;
}